import Chart from 'chart.js/auto';

export const lineChart = (ctx, labels, datapoints1, datapoints2) => {
  const data = {
    labels: labels,
    datasets: [
      {
        label: '',
        data: datapoints1,
        borderColor: 'rgb(255, 99, 132)',
        fill: false,
        cubicInterpolationMode: 'monotone',
        tension: 0.4,
      },
      {
        label: '',
        data: datapoints2,
        borderColor: 'rgb(54, 162, 235)',
        fill: false,
        cubicInterpolationMode: 'monotone',
        tension: 0.4,
      },
    ],
  };

  const config = {
    type: 'line',
    data: data,
    options: {
      responsive: true,
      plugins: {
        title: {
          display: true,
          text: '112',
        },
      },
      interaction: {
        intersect: false,
      },
      scales: {
        x: {
          display: true,
          title: {
            display: true,
          },
        },
        y: {
          display: true,
          title: {
            display: true,
            text: '()',
          },
          suggestedMin: 0,
          suggestedMax: 40,
        },
      },
    },
  };

  return new Chart(ctx, config);
};

export const barChart = (ctx, labels, datapoints) => {};
